.App {
  font-family: "Josefin Sans", sans-serif;
  /* display: grid;
  grid-template-columns: 300px auto;
  grid-template-rows: 100px auto auto; */
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  overflow: auto; 
  background: #F3904F;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #3B4371, #F3904F);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#3B4371), to(#F3904F));
  background: -webkit-linear-gradient(left, #3B4371, #F3904F);
  background: linear-gradient(to right, #3B4371, #F3904F); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  
  /* color: white; */
}




